.ButtonPad {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: auto;
	grid-column-gap: 10px;
	grid-row-gap: 10px;
}

.btn {
	position: relative;
	width: 90px;
	height: 90px;
	border: 1px solid rgba(255,105,22, .5);
	border-radius: 2px;
	background: rgba(255,105,22, .5);
	box-shadow: 3px 3px rgba(255, 105, 22, .9);
	font-size: 25px;
	color: white;
	cursor: pointer;
}

.btn:active {
	box-shadow: 2px 1px rgba(255, 105, 22, .9);
	top: 2px;
}

.btn-active {
	box-shadow: 2px 1px rgba(255, 105, 22, .9);
	top: 2px;
}
