@import url('https://fonts.googleapis.com/css?family=Orbitron|Oswald');

.ControlPad {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	border: 1px solid rbga(200,200,200,0.5);
	width: 30%;
	height: 70%;
	font-family: 'Oswald', sans-serif;
	color: #333;
}

.toggleControl {
	display: flex;
	align-items: center;
	justify-content: space-between;	
}

.information-screen {
	width: 70%;
	margin: 15px auto;
	padding: 25px;
	background: #333;
	border-radius: 5px;
	color: rgb(255,105,22);
	text-align: center;
	font-family: 'Orbitron', sans-serif;
	font-weight: 700;
	font-size: 1.1rem;
}

h3{
	display: inline-block;
	margin: 0;
	padding: 0;
	color: rbga(200,200,200,0.5);
}

input[type="checkbox"] {
	width: 0;
	height: 0;
	visibility: hidden;
}

label {
	cursor: pointer;
	text-indent: -9999px;
	width: 80px;
	height: 30px;
	background: grey;
	display: block;
	border-radius: 100px;
	position: relative;
}

label:after {
	content: '';
	position: absolute;
	top: 5px;
	left: 5px;
	width: 20px;
	height: 20px;
	background: #fff;
	border-radius: 50%;
	transition: 0.3s ease-in-out;
}

input:checked + label {
	background: #bada55;
}

input:checked + label:after {
	left: calc(100% - 5px);
	transform: translateX(-100%);
}

label:active:after {
	width: 130px;
}

.volume-control {
	-webkit-appearance: none; 
	width: 100%;   
	height: 7px;                
	border-radius: 5px;         
	background-color: #18003e;  
	outline: none;              
	padding: 0;              
	margin: 0;
}

.volume-control::-webkit-slider-thumb {
	padding: 0;
	margin: 0;
	-webkit-appearance: none;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	background: rgb(255,105,22);
	cursor: pointer;
}

.volume-control::-webkit-slider-thumb:hover {
	background: rgb(185,85,22);
}
